import React from "react";
import { useHistory } from "react-router-dom";

export const EmptyCart = () => {
    const history = useHistory();

    return (
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <h3>Không có khoá học nào trong giỏ</h3>
                </div>
            </div>
            <div className="row">
                <div className="col-12">
                    <div className="list-empty">
                        <img
                            src="/Images/empty-cart.png"
                            alt=""
                            style={{ width: 126, height: 120, marginBottom: 30 }}
                        />
                        <p>Giỏ hàng đang trống,tiếp tục mua sắm để tìm được khoá học ưng ý nào</p>
                        <button type="button" className="btn btn-danger" onClick={() => history.push("/home")}>
                            Tiếp tục mua sắm
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};
export default EmptyCart;
